// src/pages/splash/Splash.js

import React, { Component } from "react";
import "./Splash.css";
import { Navigate } from "react-router-dom";
import LoaderLogo from "../../components/Loader/LoaderLogo.js";

class Splash extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false,
    };
  }

  componentDidMount() {
    this.id = setTimeout(() => this.setState({ redirect: true }), 5500);
  }

  componentWillUnmount() {
    clearTimeout(this.id);
  }

  render() {
    // Once redirect flag is true, navigate to /home
    if (this.state.redirect) {
      return <Navigate to="/home" replace />;
    }

    // Otherwise, show the animation
    return (
      <div className="logo_wrapper">
        <div className="screen" style={{ backgroundColor: this.props.theme.text }}>
          <LoaderLogo id="logo" theme={this.props.theme} />
        </div>
      </div>
    );
  }
}

export default Splash;
