// src/containers/Main.js

import React, { Component } from "react";
import { HashRouter, Routes, Route, Navigate } from "react-router-dom";

import Home from "../pages/home/HomeComponent";
import Splash from "../pages/splash/Splash";
import Education from "../pages/education/EducationComponent";
import Experience from "../pages/experience/Experience";
import Opensource from "../pages/opensource/Opensource";
import Contact from "../pages/contact/ContactComponent";

// import Projects from "../pages/projects/Projects";
// import Achievements from "../pages/achievements/Achievements";

import { settings } from "../portfolio.js";

export default class Main extends Component {
  render() {
    const theme = this.props.theme;

    return (
      <HashRouter basename="/">
        <Routes>
          {settings.isSplash ? (
            /* Splash-mode routes */
            <>
              <Route path="/" element={<Splash theme={theme} />} />
              <Route path="/splash" element={<Splash theme={theme} />} />
            </>
          ) : (
            /* Normal portfolio routes */
            <>
              <Route path="/" element={<Home theme={theme} />} />
              <Route path="/home" element={<Home theme={theme} />} />
              <Route
                path="/opensource"
                element={<Opensource theme={theme} />}
              />
              {/* 🆕 Projects & Achievements routes */}
            </>
          )}

          {/* Always available */}
          <Route
            path="/experience"
            element={<Experience theme={theme} />}
          />
          <Route
            path="/education"
            element={<Education theme={theme} />}
          />
          {/* <Route
            path="/projects"
            element={<Projects theme={theme} />}
          /> */}
          <Route
            path="/contact"
            element={<Contact theme={theme} />}
          />

          {/* Fallback: redirect unknown URLs back to root */}
          <Route path="*" element={<Navigate replace to="/" />} />
        </Routes>
      </HashRouter>
    );
  }
}
